.level51-cu-uploadedFile {
  margin: 5px;
  padding: 10px;
  position: relative;
  text-align: center;
  display: flex;
  flex-direction: column;
  border: 2px dashed #cccccc;
  border-radius: 5px;
  width: calc(25% - 5px * 2);
}
.level51-cu-uploadedFile .level51-cu-imageContainer {
  margin-bottom: 10px;
  height: 60px;
}
.level51-cu-uploadedFile .level51-cu-imageContainer img {
  max-height: 100%;
  max-width: 100%;
  display: block;
  margin: 0 auto;
}
.level51-cu-uploadedFile .level51-cu-fileMetaInfo {
  position: absolute;
  top: 5px;
  right: 5px;
  cursor: help;
}
.level51-cu-uploadedFile .level51-cu-fileInfo {
  flex: 1 1 auto;
  min-width: 0;
  /* 1 */
  min-height: 0;
  /* 1 */
}
.level51-cu-uploadedFile .level51-cu-uploadedFile-actions {
  justify-self: flex-end;
  margin-top: 10px;
  display: flex;
  justify-content: center;
}
.level51-cu-uploadedFile .level51-cu-uploadedFile-actions a.btn {
  display: flex;
  align-items: center;
}
.level51-cu-uploadedFile .level51-cu-uploadedFile-actions .btn-sm {
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  line-height: 1.5;
  border-radius: 0.2rem;
  display: flex;
  align-items: center;
}
.level51-cu-uploadedFile .level51-cu-uploadedFile-actions .btn-sm[class*=font-icon-]:before {
  font-size: 1rem;
}
